import React from 'react';
import "./Accueil.css";
import ImageDeGarde from "../Components/ImageDeGarde";
import AccueilAboutUs from "../Components/Accueil/AccueilAboutUs";
import AccueilTeam from "../Components/Accueil/AccueilTeam";
import AccueilCAS from "../Components/Accueil/AccueilCAS";
import ImageVide from "../Assets/ImgVide.png";

export default class Accueil extends React.Component{
    constructor(props){
        super(props);
    }

    render() {
        return(
            <section id="Accueil-Container">
                <div className={"Pages"}>
                    <div className={"LeftColumn"}/>
                    <div className={"MiddleColumn"} id={"Content_AccueilContainer"}>
                        <ImageDeGarde Page={"Accueil"}/>
                        <img className={"emptyImage"} src={ImageVide}/>
                        <AccueilAboutUs/>
                        <AccueilTeam/>
                        <AccueilCAS/>
                    </div>
                    <div className={"RightColumn"}/>
                </div>
            </section>
        )
    }
}